import React from 'react';
import './Quiz.css';

const OptionComponent = (props) => {
    console.log("OptionComponent.options = " + props.options);

    const handleClick = (option) => {
        props.setSelectedOption(option.id);
        props.setSubmitted(false);
    }

    return (
        <div>
            <div className="container mt-sm-5 my-1">
                <div className="question ml-sm-5 pl-sm-5 pt-2">
                    <div className="ml-md-3 ml-sm-3 pl-md-5 pt-sm-0 pt-3" id="options">
                        {
                            props.options !== undefined &&
                            props.options.map((option) => {
                                return (
                                    <label className="options" key={option.id} onClick={() => handleClick(option)}>
                                        {option.text}
                                        <span className={`checkmark ${props.selectedOption === option.id ? 'selected' : ''}`}></span>
                                    </label>
                                )
                            })
                        }
                    </div>
                </div>
            </div>
        </div>
    )
}

export default OptionComponent;
