import React from 'react';
import './Quiz.css';

const AnswerComponent = (props) => {

    console.log("AnswerComponent props.answers", props.answers);
    console.log("AnswerComponent props.selectedOption", props.selectedOption);

    const successMessage = () => {
        return (
            <div>
                {
                    props.answers.map((answer) => {
                        return (<div>
                            <h1>success</h1>
                            <p>{answer.text}</p>
                        </div>);
                    })
                }
            </div>
        )
    }


    const errorMessage = () => {
        return (<div>
            <h1>Wrong Answer</h1>
            <p>Try Again.</p>
        </div>);
    }

    const renderData = () => {
        return (<div className="container mt-sm-5 my-1">
            <div className="question ml-sm-5 pl-sm-5 pt-2">
                {props.answers[0].rightOptionId === props.selectedOption ? successMessage() : errorMessage()}
            </div>
        </div>);
    }

    return (
        (props.selectedOption !== undefined && props.answers !== undefined && props.answers.length > 0) ? renderData() : <></>
    );
}

export default AnswerComponent;