import React, { useState, useEffect, useCallback } from 'react';
import { AnimaGetRequest } from '../models/AnimaGetRequest';
import { ErrorResponse } from '../models/ErrorResponse';
import Quiz2Container from './Quiz2Container';

const StartQuiz = () => {

    const [questions, setQuestions] = useState([]);
    const [error, setError] = useState(new ErrorResponse());
    const [loading, setLoading] = useState(true);
    const [notStarted, setNotStarted] = useState(true);
    const [currentPage, setCurrentPage] = useState(0);

    const loadQuestionBucket = useCallback(async () => {

        console.log("loadQuestionBucket {");
        setLoading(true);
        var animaGetRequest = new AnimaGetRequest();
        animaGetRequest.pagination.page = currentPage;
        console.log("animaGetRequest", animaGetRequest);
        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(animaGetRequest)
        };
        try {
            const res = await fetch('http://ec2-3-110-219-66.ap-south-1.compute.amazonaws.com:8080/quiz/api/v1/question/get', requestOptions);
            if (!res.ok) {
                throw new Error('something went wrong');
            }
            const resJson = await res.json();
            console.log("resJson = ", resJson);
            setQuestions(resJson.data);
            console.log("questions = ", questions);

        } catch (error) {
            setError(new ErrorResponse(true, error));
        }
        setLoading(false);
        console.log("loadQuestionBucket }");
    }, [currentPage]);

    useEffect(() => {
        loadQuestionBucket();
    }, [loadQuestionBucket]);

    const startTest = () => {
        console.log("startTest {");
        //loadQuestionBucket();
        setCurrentPage(0);
        setNotStarted(false);
        console.log("startTest }");
    }

    const renderStartTest = () => {
        return (
            <div className="d-flex align-items-cennter pt-3">
                <div id="next"> <button className="btn btn-primary" type='button'
                    onClick={() => startTest()}>Start Test</button> </div>
            </div>
        )
    }

    return (
        <div>
            {notStarted && renderStartTest()}
            {!notStarted && !loading && <Quiz2Container questions={questions} currentPage={currentPage} setCurrentPage={setCurrentPage} />}
            {!notStarted && loading && <p>loading...</p>}
            {!notStarted && error.status && <p>Error happen! {error.msg}</p>}
        </div>
    )
}

export default StartQuiz;

