import React, { useState, useEffect } from 'react';
import { Answer } from '../models/Answer';
import AnswerComponent from './AnswerComponent';
import OptionComponent from './OptionComponent';
import QuestionComponent from './QuestionComponent';


const Quiz2Container = (props) => {

    console.log("Quiz2Container->props.questions = ", props.questions);

    const [selectedOption, setSelectedOption] = useState();
    const [current, setCurrent] = useState(0);
    const [submitted, setSubmitted] = useState(false);

    const handleNext = () => {
        setSubmitted(false);
        console.log("handleNext: current = ", current);
        if (current === props.questions.length - 1) {
            props.setCurrentPage(props.currentPage + 1);
            console.log("handleNext: current = ", current);
            setCurrent(0);
            //setQuestion(undefined);
        } else {
            //setQuestion(props.questions[current]);
            setCurrent(current + 1);
        }


    }

    const loadAnswer = () => {
        return (
            <>
                <AnswerComponent answers={props.questions[current].answers} selectedOption={selectedOption} />
            </>
        )
    }

    const controllers = () => {
        console.log("controllers{}");
        return (
            <>
                <div className="d-flex align-items-center pt-3">
                    <div id="submit"> <button className="btn btn-primary" type='button'
                        onClick=
                        {() => setSubmitted(true)}
                    >Submit</button> </div>
                </div><div className="d-flex align-items-cennter pt-3">
                    <div id="next"> <button className="btn btn-primary" type='button'
                        onClick={() => {
                            handleNext()
                        }}>Next</button> </div>
                </div>
            </>
        );
    }

    const renderQuiz = () => {
        return (
            <>
                <QuestionComponent questionText={props.questions[current].text} />
                <OptionComponent options={props.questions[current].options} selectedOption={selectedOption}
                    setSelectedOption={setSelectedOption} setSubmitted={setSubmitted} />
                {controllers()}
                {submitted && loadAnswer()}
            </>

        )
    }

    const renderData = () => {

        return (
            <div>
                <div className="container mt-sm-5 my-1">
                    <div>
                        {props.questions[current] !== undefined ? renderQuiz() : <></>}
                    </div>
                </div>
            </div>);
    }


    return (
        props.questions !== undefined ? renderData() : <></>
    )

}

export default Quiz2Container;